  margin-top: -10px;
}
.shadow-effect:before {
  border: 1px solid #2a897b;
  border-radius: 0 0 3px 3px;
  bottom: -6px;
  content: "";
  height: 12px;
  left: 0;
  margin: 0 20px;
  opacity: 0;
  position: absolute;
  right: 0;
  transition: all 0.5s linear;
